import * as React from "react"
import { graphql, Link } from 'gatsby'
import {Helmet} from "react-helmet";

import Page from "../components/page"

import "./index.scss"

const IndexPage = ({ data }) => {
  const sortedData = data.allMdx.nodes.sort((a,b) => {return parseInt(a.slug)-parseInt(b.slug)})

  return (
    <Page title="A List of the Fables" header={<span>The Ã†sop for Newbies</span>}>
      <Helmet>
        <meta charSet="utf-8" />
        <title>GM Aesop's Fables</title>
        <meta name="description" content="Learn valuable lessons about Eternal Return with fables!" />

        <meta property="og:url" content="https://gmaesopsfables.org/" />
        <meta property="og:type" content="website" />
        <meta property="og:title" content="GM Aesop's Fables" />
        <meta property="og:description" content="Learn valuable lessons about Eternal Return with fables!" />
        <meta property="og:image" content="/src/images/icon.png" />

        <meta name="twitter:card" content="summary_large_image" />
        <meta property="twitter:domain" content="gmaesopsfables.org" />
        <meta property="twitter:url" content="https://gmaesopsfables.org/" />
        <meta name="twitter:title" content="GM Aesop's Fables" />
        <meta name="twitter:description" content="Learn valuable lessons about Eternal Return with fables!" />
        <meta name="twitter:image" content="/src/images/icon.png" />
      </Helmet>
      <ul className="index__list">
      {
        sortedData.map(node => (
          <li className="index__item" key={node.slug}>
            <Link className='index__link' to={`/fables/${node.slug}`}>{node.frontmatter.title}</Link>
          </li>
        ))
      }
      </ul>
      <h3>More coming soon!</h3>
    </Page>
  )
}

export const query = graphql`
  query {
    allMdx(sort: {fields: slug, order: ASC}) {
      nodes {
        frontmatter {
          title
        }
        slug
      }
    }
  }
`

export default IndexPage
